<template>
  <div class="style-editing-panel" v-if="activeComponentID">
    <el-tabs stretch>
      <el-tab-pane label="内容" name="0">
        <div class="menu-wrapper">
          <text-editor
            :content="content"
            @change="textValueChange"
            :textStyleObject="textStyleObject"
            @textStyleChange="textStyleChange"
          />
        </div>
      </el-tab-pane>
      <el-tab-pane label="样式" name="1">
        <div class="menu-wrapper">
          <background-change
            :background="background"
            @change="backgroundChange"
          />
          <slider
            :value="opacity"
            :opts="{ placeholder: '透明度', inputBox: true, hasRate: true }"
            @valueChange="opacityChange"
          />
          <a-divider type="horizontal" />
          <div style="margin-bottom: 10px;">按钮颜色</div>
          <color-selector :color="buttonColor" @change="buttonColorChange" />
          <slider
            :value="buttonOpacity"
            :opts="{ placeholder: '透明度', inputBox: true, hasRate: true }"
            @valueChange="buttonOpacityChange"
          />
          <a-divider type="horizontal" />
          <div style="margin-bottom: 10px;">按钮尺寸</div>
          <slider
            :value="buttonWidth"
            :opts="{ placeholder: '宽度', inputBox: true, hasPX: true }"
            @valueChange="buttonWidthChange"
          />
          <slider
            :value="buttonHeight"
            :opts="{ placeholder: '高度', inputBox: true, hasPX: true }"
            @valueChange="buttonHeightChange"
          />
          <slider
            :value="buttonRadius"
            :opts="{ placeholder: '圆角', inputBox: true, hasPX: true }"
            @valueChange="buttonRadiusChange"
          />
          <a-divider type="horizontal" />
          <margin-edit
            @valueChange="marginChange"
            :customButtonMarginObject="buttonMarginObject"
          />
          <a-divider type="horizontal" />
          <div class="float-switch">
            <span>悬浮模式</span>
            <a-switch v-model="floatChecked" />
          </div>
          <div v-show="floatChecked">
            <div style="margin-top: 10px;">悬浮位置</div>
            <div class="float-loaction-group" style="margin-top: 10px;">
              <a-radio-group
                name="float-location-radio-group"
                @change="buttonFloatChange"
                :defaultValue="defaultPosition"
              >
                <a-radio :value="'tl'">左上方</a-radio>
                <a-radio :value="'tc'">顶部中间</a-radio>
                <a-radio :value="'tr'">右上方</a-radio>
                <a-radio :value="'bl'">左下方</a-radio>
                <a-radio :value="'bc'">底部中间</a-radio>
                <a-radio :value="'br'">右下方</a-radio>
              </a-radio-group>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="动画" name="2"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import slider from "@/components/StyleMenuComponents/slider.vue";
import bgChange from "@/components/StyleMenuComponents/backgroundChange.vue";
import MarginEdit from "@/components/StyleMenuComponents/marginEdit.vue";
import colorSelector from "@/components/StyleMenuComponents/colorSelector.vue";
import textEditor from "@/components/StyleMenuComponents/textEditor.vue";
import mixinObject from "@/mixin.js";

const {
  componentStoreBaseOperate,
  backgroundUpdateOperate,
  opacityUpdateOperate,
  marginUpdateOperate
} = mixinObject;
export default {
  mixins: [
    componentStoreBaseOperate,
    backgroundUpdateOperate,
    opacityUpdateOperate,
    marginUpdateOperate
  ],
  components: {
    slider: slider,
    "color-selector": colorSelector,
    "background-change": bgChange,
    "margin-edit": MarginEdit,
    "text-editor": textEditor
  },
  data() {
    return {
      floatChecked: false,
      previewColor: "#e8e8e8",
      editorOption: {
        modules: {
          toolbar: "#toolbar"
        }
      }
    };
  },
  methods: {
    buttonFloatChange(e) {
      let location = e.target.value;
      let payload = {
        id: this.activeComponentID,
        value: {
          position: location
        }
      };
      this.updateComponentInfo(payload);
    },
    textValueChange(val) {
      let payload = {
        id: this.activeComponentID,
        value: {
          content: val
        }
      };
      this.updateComponentInfo(payload);
    },
    updateValue(message) {
      let payload = {
        id: this.activeComponentID,
        value: {
          style: {
            [message.key]: `${message.value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    colorChange(value) {
      this.quill.format("color", value);
      this.previewColor = value;
    },
    cleanIcon() {},
    buttonOpacityChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            opacity: value / 100
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    buttonColorChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            background: value
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    buttonWidthChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            width: `${value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    buttonHeightChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            height: `${value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    buttonRadiusChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            "border-radius": `${value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    cleanFloatStyle() {
      let payload = {
        id: this.activeComponentID,
        value: {
          position: ""
        }
      };
      this.updateComponentInfo(payload);
    },
    textStyleChange(val) {
      let payload = {
        id: this.activeComponentID,
        value: {
          textStyle: val
        }
      };
      this.updateComponentInfo(payload);
    },
    marginChange(message) {
      let payload = {
        id: this.activeComponentID,
        value: {
          buttonStyle: {
            [message.key]: `${message.value}px`
          }
        }
      };
      this.updateComponentInfo(payload);
    }
  },
  watch: {
    floatChecked(val) {
      // 避免产生无效的Undo Event
      if (!val && this.defaultPosition) {
        this.cleanFloatStyle();
      }
    }
  },
  computed: {
    textStyleObject() {
      return this.activatedComponent.componentInfo.textStyle;
    },
    defaultPosition() {
      return this.activatedComponent.componentInfo.position;
    },
    content() {
      return this.activatedComponent.componentInfo.content;
    },
    buttonOpacity() {
      return this.activatedComponent.componentInfo.buttonStyle.opacity * 100;
    },
    buttonColor() {
      return this.activatedComponent.componentInfo.buttonStyle.background;
    },
    buttonWidth() {
      return this.activatedComponent.componentInfo.buttonStyle.width;
    },
    buttonHeight() {
      return this.activatedComponent.componentInfo.buttonStyle.height;
    },
    buttonRadius() {
      return this.activatedComponent.componentInfo.buttonStyle["border-radius"];
    },
    buttonMarginObject() {
      return {
        "margin-top": parseInt(
          this.activatedComponent.componentInfo.buttonStyle["margin-top"]
        ),
        "margin-right": parseInt(
          this.activatedComponent.componentInfo.buttonStyle["margin-right"]
        ),
        "margin-bottom": parseInt(
          this.activatedComponent.componentInfo.buttonStyle["margin-bottom"]
        ),
        "margin-left": parseInt(
          this.activatedComponent.componentInfo.buttonStyle["margin-left"]
        )
      };
    }
  },
  mounted() {
    if (this.defaultPosition) {
      this.floatChecked = true;
    }
  }
};
</script>

